@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary: #6948ff;
  --secondary: #553bcb;
  --darkgrey: #22262a;
  --lightgrey: #d3d3d3;
  --grey: #808080;
}

body {
color: #333;
font-family: 'Montserrat', sans-serif;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  columns: #fff;
}

.primary-color {
  color: #6948ff;
  color: var(--primary);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  width: 100%;
  padding: 8px 0;
  border-radius: 4px;
  border: 1px solid #6948ff;
  border: 1px solid var(--primary);
  background-color: #6948ff;
  background-color: var(--primary);
  color: #fff;
  cursor: pointer;
}

button:hover {
  border: 1px solid #553bcb;
  border: 1px solid var(--secondary);
  background-color: #553bcb;
  background-color: var(--secondary);
}
header {
    width: 100%;
    height: 100px;
    background-color: var(--darkgrey);
}

header .navbar {
    max-width: 1240px;
    height: 100%;
    padding: 0 1rem;
    margin: auto;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
}

header .logo {
    display: flex;
    align-items: center;
}

header .logo img {
    width:80px;
    height: auto;
}

.hamburger {
    display: none;
    position: absolute;
    top: 2rem;
    right: 1rem;
}

.nav-menu {
    display: flex;
    align-items: center;
    justify-self: start;
}

.nav-item {
    padding: 0 1.5rem;
}

.nav-link {
    color: #fff;
}

@media screen and (max-width:960px) {
    .hamburger {
        display: block;
    }
    .nav-menu {
        position: fixed;
        left: -100%;
        flex-direction: column;
        justify-content: center;
        background-color: var(--darkgrey);
        width: 100%;
        height: 100%;
        text-align: center;
        transition: 0.4s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 1.5rem 0;
        font-size: 1.8rem;
    }
}







.section-faq {
    width: 100%;
    margin: 3rem 0;
}

.section-faq .container {
    max-width: 1240px;
    padding: 1rem;
    margin: auto;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 2fr;
}

.section-faq h3 {
    font-size: 28px;
    padding: 1rem 0;
}

.section-faq .col-1 p {
    color: var(--grey);
}

.section-faq span {
    color: var(--primary);
}

.section-faq h5 {
    font-size: 1rem;
    padding: .4rem 0;
}

.section-faq .faq {
    padding: 1.2rem 0;
}

.section-faq .faq p {
    color: var(--grey);
}


@media screen and (max-width:960px) {
    .section-faq {
        margin: 1rem 0;
    }

    .section-faq .container {
        grid-template-columns: 1fr;
    }
}





.footer {
    width: 100%;
    background-color: var(--darkgrey);
}

.footer .container {
    max-width: 1240px;
    padding: 3rem 1rem;
    margin: auto;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
}

.footer h6,p {
    color: var(--grey);
    font-size: 14px;
    padding: .4rem 0;
}

.footer .link {
    color: var(--lightgrey);
    font-size: 14px;
    padding: .4rem 0;
    display: block;
}

.footer .subscribe {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer input {
    padding: .4rem;
    font-size: 14px;
    border-radius: 4px;
    width: 250px;
    border: 1px solid var(--lightgrey);
}

.footer button {
    margin-left: .8rem;
    width: 150px;
}

.footer .content {
    max-width: 1240px;
    margin: auto;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--grey)
}


@media screen and (max-width:960px) {
    .footer .container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 1rem;
    }

    .footer .solutions, .support {
        display: none;
    }

    .footer .col-subscribe {
        margin: 2rem;
        text-align: center;
    }

    .footer .subscribe {
        flex-direction: column;
    }

    .footer input {
        width: 100%;
    }

    .footer button {
        width: 100%;
        margin: .2rem 0;
    }

    .footer-bottom .content {
        padding: 1rem;
        justify-content: center;
    }

    .footer-bottom .right {
        display: none;
    }
}
















.hero {
    width: 100%;
    height: 100vh;
    background-color: var(--darkgrey);
}

.hero .container {
    max-width: 1240px;
    padding: 0 1rem;
    margin: auto;
}

.hero .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.hero .col-1 {
    padding: 1rem 1rem 1rem 0;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hero .col-1 h1 {
    font-size: 42px;
    font-weight: 700;
    color: #fff;
}

.hero .col-1 p {
    font-size: 1rem;
    color: var(--grey);
    line-height: 1.4;
    margin-top: 1.2rem;
}

.hero .used-by p {
    color: var(--lightgrey);
    font-size: 12px;
    margin: 2rem 0 1rem 0;
}

.hero .col-1 .icons i {
    color: var(--grey);
    padding-right: 1rem;
}


.hero .col-2 {
    padding: 1rem;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: .8rem;
}

.hero .col-2 p {
    color: var(--grey);
}

.hero .form-layout {
    width: 400px;
    margin: auto;
    background-color: #fff;
    border-radius: 6px;
}

.hero .form-container {
    padding: 1.5rem 2rem;
}

.hero .sign-in-txt {
    margin-bottom: 10px;
}

.social-login i {
    background-color: #fff;
    padding: 16px 45px 6px 45px;
    border: 1px solid var(--lightgrey);
    border-radius: 4px;
}


.col-2 .divider p {
    width: 100%;
    text-align: center;
    border-bottom: 1px solid var(--lightgrey);
    line-height: .1em;
    margin: 2rem 0 2rem;
}

.col-2 .divider p span {
    background-color: #fff;
    padding: 0 10px;
}

form input {
    display: block;
    width: 100%;
    margin: 1.2rem 0;
    padding: 8px;
    border: 1px solid var(--lightgrey);
    border-radius: 4px;
}

.hero .form-footer {
    border-top: 1px solid var(--lightgrey);
    background-color: #f8f8f8;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}

.form-footer p {
    padding: 1.5rem 2rem;
}

.form-footer span {
    font-weight: 500;
}

@media screen and (max-width:960px) {
    .hero {
        height: auto;
        width: 100%;
    }

    .hero .container {
        padding: 0 1rem;
    }

    .hero .content {
        grid-template-columns: 1fr;
    }

    .hero .col-1 {
        height: 90vh;
        padding: .2rem;
        max-width: 100%;
    }

    .hero h1 {
       font-size: 32px;
    }

    .hero .col-2 {
        display: none;
    }

    
}

.pricing {
    width: 100%;
    margin-top: 5rem;
}

.pricing .container {
    max-width: 1240px;
    padding: 1rem;
    margin: auto;
    overflow: hidden;
}

.pricing .top-content {
    display: grid;
    grid-template-columns: 3fr 1fr;
    border-bottom: 1px solid var(--lightgrey);
    padding-bottom: 3rem;
}

.pricing .top-content h2 {
    font-size: 38px;
    padding-bottom: .5rem;
}

.pricing .top-content span {
    color: var(--primary);
}
.pricing .top-content p {
    color: var(--grey);
}

.pricing .bottom-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    margin: 3rem auto;
}

.pricing .card {
    display: flex;
    flex-direction: column;
    padding: 1rem 1rem 1rem 0;
}

.pricing .card div {
    display: flex;
    border-bottom: 1px solid var(--lightgrey);
    padding: 1rem 0;
}

.pricing .card .no-border {
    border: none;
}


.pricing .card span {
    color: var(--primary);
    text-transform: uppercase;
    font-weight: 500;
    font-size: 14px;
}

.pricing .card h3 {
    font-size: 28px;
    margin: .7rem;
}

@media screen and (max-width:960px) {
    .pricing .top-content {
        grid-template-columns: 1fr;
    }

    .pricing .container .bottom-content {
        display: none;
    }
    .pricing .top-content h2 {
        font-size: 32px;
    }
}








.testimonial {
    width: 100%;
    color: #fff;
    margin: 3rem 0;
}

.testimonial .container {
    max-width: 1240px;
    padding: 1rem;
    margin: auto;
    overflow: hidden;
}

.testimonial .outline {
    background-image: linear-gradient(to left, rgba(73, 50, 173, 0.5),rgba(73, 50, 173, 0.9)),
    url('https://images.unsplash.com/photo-1562071707-7249ab429b2a?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1650&q=80');
    width: 100%;
    height: 450px;
    background-position: left;
    border-radius: 10px;
}

.testimonial .content {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 50%;
    justify-content: center;
    padding: 3rem;
}


.testimonial .body {
    padding: 1.2rem 0;
}

.testimonial .name p {
    font-size: 14px;
    color: #fff;
}

@media screen and (max-width:960px) {
    .testimonial {
        margin: 1rem 0;
    }

    .testimonial .content {
        max-width: 100%;
    }
}




.contact {
    width: 100%;
    margin: 3rem 0;
    display: flex;
}

.contact .container {
    max-width: 1240px;
    margin: auto;
    overflow: hidden;
    display:flex;
}

.contact .col-1 {
    flex: 1 0 45%;
    padding: 5rem 2rem;
}

.contact .col-1 h2 {
    font-size: 28px;
    padding: 0.4rem 0;
}

.contact .p {
    color: var(--grey);
}

.contact .col-1 .address {
    padding: 1rem 0;
}

.contact .col-1 .icons {
    display: flex;
    align-items: center;
    padding: .4rem 0;
}


.contact .careers {
    padding: 2rem 0;
}

.contact span {
    color: var(--primary);
}

.contact .col-2 {
    flex: 1 0 55%;
    padding: 5rem 2rem;
}


.contact form {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin: auto;
}

.contact form input {
    margin-bottom: 1rem;
    padding: .6rem;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid var(--lightgrey);
}

.contact form textarea {
    margin-bottom: 1rem;
    padding: 0.6rem;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid var(--lightgrey);
    font-family: 'Montserrat', sans-serif;
}

.contact form .checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 0;
}

.contact .checkbox input {
    margin: 0;
    padding-right: 4px;
}



@media screen and (max-width:960px) {
    .contact {
        margin: 1rem 0;
    }

    .contact .container {
        flex-direction: column;
    }

    .contact .col-1 {
        padding: 0 1rem;
    }

    .contact .col-2 {
        padding: 1rem;
    }
}




